            </div>
            
            <!-- Social Links Collapsible Section -->
            <div class="form-group">
              <div class="collapsible-header" @click="showSocialLinks = !showSocialLinks">
                <label>Optional Links</label>
                <i :class="['fas', showSocialLinks ? 'fa-chevron-up' : 'fa-chevron-down']"></i>
              </div>
              
              <div class="collapsible-content" :class="{ 'open': showSocialLinks }">
                <div class="social-links-container">
                  <div class="form-group">
