                  {{ option.label }}
                </button>
              </div>
              <div class="dev-buy-info">
                <div class="info-icon">
                  <i class="fas fa-info-circle"></i>
                </div>
                <div class="info-text">
                  <p><strong>Dev buy</strong> will be executed using the total squad funds collected. These purchased tokens will be automatically locked for the selected time period to ensure project safety and prevent rug pulls.</p>
                </div>
              </div>
            </div>

            <!-- select min/max squad size -->
            
            <div class="form-group">
