              <a :href="'https://solscan.io/token/' + createdToken.address" target="_blank">
                {{ createdToken.address }}
              </a>
            </p>
          </div>
          <div class="actions">
            <button class="action-btn" @click="copyTokenAddress">
              <i class="fas fa-copy"></i> Copy Address
            </button>
