<script>
import { ref, reactive, computed, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import store from '@/vuex';
import axios from 'axios';
import Toast from '@/components/Toast.vue';

export default {
  name: 'MemeTokenLaunchPage',
  components: {
    AppToast: Toast
  },
  setup() {
    const router = useRouter();
    
    // Состояние для toast
    const toast = reactive({
      show: false,
      message: '',
      type: 'info',
      duration: 3000
    });
    
    // Состояние для отслеживания успеха и ошибок
    const success = ref(false);
    const error = ref('');
    const showSocialLinks = ref(false);
    const showMore = ref(false);
    
    // State
    const formData = reactive({
      name: '',
      symbol: '',
      logo: null,
      description: '',
      twitter: '',
      telegram: '',
      website: '',
      unlockVia: '10m', // Default unlock time
      lottery: false,
      squadSize: '1-3.5'
    });
    
    const unlockOptions = [
      { label: '10m', value: '10m' },
      { label: '30m', value: '30m' },
      { label: '1h', value: '1h' },
      { label: '2h', value: '2h' },
      { label: '1d', value: '1d' },
      { label: '1w', value: '1w' }
    ];
    
    const squadSizeOptions = [
      { value: '0.5-2.5', min: '0.5', max: '2.5', recommended: false },
      { value: '1-3.5', min: '1', max: '3.5', recommended: true },
      { value: '1.5-5', min: '1.5', max: '5', recommended: false }
    ];
    
    const errors = reactive({
      name: '',
      symbol: '',
      logo: ''
    });
    
    const logoPreview = ref(null);
    const isSubmitting = ref(false);
    const showSuccessModal = ref(false);
    const createdToken = reactive({
      name: '',
      symbol: '',
      address: ''
    });
    
    // Computed
    const walletConnected = computed(() => store.getters.walletAddress);
    
    // Methods
    
    const handleLogoUpload = (event) => {
      const file = event.target.files[0];
      if (!file) return;
      
      // Проверка типа файла
      if (!['image/jpeg', 'image/png'].includes(file.type)) {
        errors.logo = 'Logo must be a PNG or JPG file';
        return;
      }
      
      // Проверка размера файла (3MB = 3145728 bytes)
      if (file.size > 3145728) {
        errors.logo = 'Logo must be less than 3MB';
        return;
      }
      
      // Сбрасываем ошибку
      errors.logo = '';
      formData.logo = file;
      
      // Создаем превью
      const reader = new FileReader();
      reader.onload = (e) => {
        logoPreview.value = e.target.result;
      };
      reader.readAsDataURL(file);
    };
    
    const validateForm = () => {
      let isValid = true;
      
      // Валидация имени
      if (!formData.name.trim()) {
        errors.name = 'Token name is required';
        isValid = false;
      } else if (formData.name.length > 50) {
        errors.name = 'Token name must be less than 50 characters';
        isValid = false;
      } else {
        errors.name = '';
      }
      
      // Валидация символа
      if (!formData.symbol.trim()) {
        errors.symbol = 'Token symbol is required';
        isValid = false;
      } else if (formData.symbol.length > 10) {
        errors.symbol = 'Token symbol must be less than 10 characters';
        isValid = false;
      } else {
        errors.symbol = '';
      }
      
      // Валидация лого
      if (!formData.logo) {
        errors.logo = 'Token logo is required';
        isValid = false;
      }
      
      return isValid;
    };
    
    const handleSubmit = async () => {
      if (!validateForm()) return;
      
      try {
        isSubmitting.value = true;
        
        // Создаем FormData для отправки данных и файла напрямую
        const tokenData = new FormData();
        if (formData.logo) {
          tokenData.append('logo', formData.logo);
        }
        
        // Добавляем все остальные данные в FormData
        tokenData.append('name', formData.name);
        tokenData.append('symbol', formData.symbol);
        tokenData.append('description', formData.description || `${formData.name} Token`);
        tokenData.append('twitter', formData.twitter || '');
        tokenData.append('telegram', formData.telegram || '');
        tokenData.append('website', formData.website || '');
        tokenData.append('unlockVia', formData.unlockVia);
        tokenData.append('lottery', formData.lottery);
        tokenData.append('squadSize', formData.squadSize);
        
        // Отправляем данные на сервер
        const instructionsResponse = await axios.post('/launch-meme-token/create', tokenData, {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });
        
        console.log('Server response:', instructionsResponse.data);
        
        // Обрабатываем ответ сервера
        if (instructionsResponse.data.success) {
          formData.name = '';
          formData.symbol = '';
          formData.logo = null;
          formData.description = '';
          formData.twitter = '';
          formData.telegram = '';
          formData.website = '';
          formData.unlockVia = '10m';
          formData.lottery = false;
          formData.squadSize = '1-3.5';
          logoPreview.value = null;
          
          // Получаем tokenId из ответа
          const { tokenId } = instructionsResponse.data.tokenLaunch;
          
          // Перенаправляем на страницу токена
          // setTimeout(() => {
            isSubmitting.value = false;
            success.value = true;
            error.value = '';
            router.push(`/token/${tokenId}`);
          // }, 100); // Небольшая задержка, чтобы пользователь увидел сообщение об успехе
        } else {
          throw new Error(instructionsResponse.data.message || 'Failed to create token');
        }
        
        // 3. Десериализуем транзакцию
        // Показываем уведомление об успехе
        // toast.show = true;
        // toast.message = 'Token created successfully!';
        // toast.type = 'success';
        // toast.duration = 3000;
      } catch (error) {
        console.error('Error creating token:', error);
        toast.show = true;
        toast.message = error.response?.data?.message || 'Failed to create token. Please try again.';
        toast.type = 'error';
        toast.duration = 3000;
      } finally {
        isSubmitting.value = false;
      }
    };
    
    const copyTokenAddress = () => {
      navigator.clipboard.writeText(createdToken.address);
      toast.show = true;
      toast.message = 'Token address copied to clipboard!';
      toast.type = 'success';
      toast.duration = 3000;
    };

    const handleSymbolInput = (event) => {
      // Преобразуем в uppercase и фильтруем только буквы
      let value = event.target.value.toUpperCase();
      value = value.replace(/[^A-Z]/g, ''); // Оставляем только заглавные буквы
      
      // Обновляем значение в formData
      formData.symbol = value;
      
      // Обновляем значение в поле ввода
      event.target.value = value;
      
      // Валидация символа
      if (value.length === 0) {
        errors.symbol = 'Token symbol is required';
      } else if (value.length > 8) {
        errors.symbol = 'Token symbol must be less than 8 characters';
      } else {
        errors.symbol = '';
      }
    };

    const isAuthenticated = computed(() => store.getters.isAuthenticated);
    
    onMounted(() => {
      // Проверяем, подключен ли кошелек
      // if (!walletConnected.value) {
      //   toast.show = true;
      //   toast.message = 'Please connect your wallet to create a token';
      //   toast.type = 'info';
      //   toast.duration = 5000;
      // }
    });
    
    return {
      formData,
      errors,
      logoPreview,
      isSubmitting,
      walletConnected,
      showSuccessModal,
      createdToken,
      unlockOptions,
      squadSizeOptions,
      isAuthenticated,
      
      // Methods
      handleLogoUpload,
      handleSubmit,
      copyTokenAddress,
      handleSymbolInput,
      toast,
      success,
      error,
      showSocialLinks,
      showMore
    };
  }
};
</script>

